import React, { Component } from "react";
import "./css/styleButtonFixed.css";

import { MdKeyboardArrowUp } from "react-icons/md";
import {
  AiOutlineMail,
  AiOutlineWhatsApp,
  AiOutlineInstagram,
} from "react-icons/ai";
import { RiContactsLine } from "react-icons/ri";

export default class AppButtonFixed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visibleScroll: false,
      visibleMail: false,
    };
  }

  componentDidMount() {
    let scrollComponent = this;
    document.addEventListener("scroll", (e) => {
      scrollComponent.toggleVisibleScroll();
      scrollComponent.toggleVisibleMail();
      // scrollComponent.togleHiddenMail()
    });
  }

  togleHiddenMail = () => {
    // console.log('1-> ' + window.pageYOffset)
    // console.log('2-> ' + document.body.scrollHeight)
    // console.log('3-> ' + document.body.scrollTop)
    // console.log('4-> ' + window.innerHeight)
    let b = document.body.scrollHeight - 200;
    console.log(window.pageYOffset + window.innerHeight);
    console.log(b);
    b <= window.pageYOffset + window.innerHeight
      ? this.setState({ visibleMail: false })
      : this.setState({ visibleMail: true });
  };
  toggleVisibleScroll = () => {
    window.pageYOffset > 100
      ? this.setState({ visibleScroll: true })
      : this.setState({ visibleScroll: false });
  };
  toggleVisibleMail = () => {
    let scrollBottom = window.pageYOffset + window.innerHeight;
    let heightContent = document.body.scrollHeight - 200;
    if (window.pageYOffset > 540) {
      heightContent > scrollBottom
        ? this.setState({ visibleMail: true })
        : this.setState({ visibleMail: false });
    } else {
      this.setState({ visibleMail: false });
    }
  };

  scrollToTop() {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  }

  render() {
    const { visibleScroll, visibleMail } = this.state;
    return (
      <>
        {visibleScroll && (
          <div className="btn-scrollTop" onClick={() => this.scrollToTop()}>
            <MdKeyboardArrowUp />
          </div>
        )}
        {/* <div className="btn-kontak">
                    <a href="" className="mb-Contact"><RiContactsLine /></a>
                    <a href="" className="mb-mail"><AiOutlineMail /></a>
                    <a href="" className="mb-whatsapp"><AiOutlineWhatsApp /></a>
                    <a href="" className="mb-instagram"><AiOutlineInstagram /></a>
                </div> */}
        {visibleMail && (
          <a href="mailto:darmaji.code.v@gmail.com" className="btn-mail">
            <AiOutlineMail />
          </a>
        )}
      </>
    );
  }
}

