import React, { Component } from "react";
import { BrowserRouter, Switch, Route, Link } from "react-router-dom";
import appHome from "./appHome";
import appDetail from "./appDetail";
import AppFooter from "./AppFooter";
import AppButtonFixed from "./AppButtonFixed";
// import AppMenu from './AppMenu'

import { AiOutlineEllipsis, AiOutlineClose } from "react-icons/ai";

import akuLogo from "./img/logo.png";

import "./css/styleApp.css";
import "./css/styleMenu.css";
import "./css/styleMenuKontent.css";

const NotFound = () => {
  return (
    <div>
      <h1>
        <b>404, PAGE NOT FOUND...!</b>
      </h1>
    </div>
  );
};

export default class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      itemTranslate: "",
      scrolling: true,
      classHeader: "onTop",
      classDisplayMenu: "menu-dekstop",
      classDisplayKontentMenu: false,
    };
    this.handleScroll = this.handleScroll.bind(this);
    this.handleClickMenu = this.handleClickMenu.bind(this);
  }

  componentDidMount() {
    window.addEventListener("scroll", this.handleScroll);
  }
  componentWillMount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleClickMenu() {
    this.setState({
      classDisplayKontentMenu: !this.state.classDisplayKontentMenu,
    });
  }

  handleScroll(event) {
    if (window.scrollY === 0 && this.state.scrolling === true) {
      this.setState({
        scrolling: false,
        classHeader: "onTop",
        classDisplayMenu: "menu-dekstop",
      });
    } else if (window.scrollY !== 0 && this.state.scrolling !== true) {
      this.setState({
        scrolling: true,
        classHeader: "beforeOnScroll onScroll",
        classDisplayMenu: "menu-mobile",
      });
    }
  }

  render() {
    let { classHeader, classDisplayKontentMenu } = this.state;
    return (
      <div className="App">
        <BrowserRouter>
          <header className={classHeader}>
            <div className="box-logo">
              <img className="logo-img" src={akuLogo} />
              <div className="logo-title">
                <span className="title-nama">Farid Darmaji</span>
                <span className="title-status">Junior Web Programmer</span>
              </div>
            </div>
            <div className="menu-dekstop" onClick={this.handleClickMenu}>
              <AiOutlineEllipsis />
            </div>
          </header>
          <div className="menu-mobile" onClick={this.handleClickMenu}>
            <AiOutlineEllipsis />
          </div>
          <ul
            className={
              classDisplayKontentMenu
                ? "kontent-menu kontent-menu-show"
                : "kontent-menu kontent-menu-hide"
            }
          >
            <li className="close-menu" onClick={this.handleClickMenu}>
              <AiOutlineClose />
            </li>
            <li className="menu-item">Home</li>
            <li className="menu-item">Project</li>
            <li className="menu-item">About</li>
            <li className="menu-item">Contact</li>
          </ul>
          <AppButtonFixed />
          <main>
            <Switch>
              <Route path="/" exact component={appHome} />
              <Route path="/detail/1" exact component={appDetail} />
              <Route component={NotFound} />
            </Switch>
          </main>
          <footer>
            <AppFooter />
          </footer>
        </BrowserRouter>
      </div>
    );
  }
}
